import { useEffect, useState } from "react";
import { Input, DatePicker, Button, Table } from "antd";
import { SearchOutlined } from "@ant-design/icons";
import { Row, Col, Form, Select, Menu, Tag } from "antd";

import { get_all_travel, get_filter } from "../../request/api";
import TravelCards from "./TravelCards";

const { SubMenu } = Menu;
const { Option } = Select;

const categorys = [
  { value: "休闲", label: "休闲" },
  { value: "探险", label: "探险" },
  { value: "海滨", label: "海滨" },
  { value: "城市", label: "城市" },
  { value: "自然", label: "自然" },
];

// const destinations = [
//   { value: "北京", label: "北京" },
//   { value: "临汾", label: "临汾" },
//   { value: "运城", label: "运城" },
//   { value: "重庆", label: "重庆" },
// ];

const prices = [
  { value: "0-1000", label: "1000以下" },
  { value: "1000-5000", label: "1000-5000" },
  { value: "5000-10000", label: "5000-10000" },
  { value: "10000-inf", label: "10000以上" },
];

function Filter({ onSelect }) {
  const [destinations, setDestinations] = useState([])
  // filters: {destination: { value: "北京", label: "北京" }}
  const [filters, setFilters] = useState({});
  console.log("==== Filter:", filters)

  useEffect(() => {
    get_filter()
      .then((res) => {
        const citys = res.unique_cities.map(city => {
            return { value: city, label: city };
        })
        setDestinations(citys)
      })
  }, [])

  function addFilter(category, option) {
    const newFilters = {
      ...filters,
      [category]: option
    }


  }

  function handleFilterChange(category, option) {
    // option 为空删除
    let newFilters = Object.fromEntries(
      Object.entries({...filters, ...(option ? {[category]: option} : {[category]: null})})
        .filter(([key, value]) => value !== null && value !== undefined)
    );
    console.log("handleFilterChange:", newFilters)
    setFilters(newFilters)

    newFilters = Object.fromEntries(
      Object.entries(newFilters)
        .map(([key, {value}]) => [key, value])
    );
    onSelect(newFilters)
  }

  function handleClear() {
    setFilters({});
  }

  function handleSubmit() {
    const filteredFilters = Object.fromEntries(
      Object.entries(filters).map(([key, value]) => [key, value.value])
    );
    console.log("handleSubmit", filteredFilters)
    onSelect(filteredFilters);
  }

  return (
    <>
      <Menu mode="horizontal">
        {/* <Menu.SubMenu title="类型" key="类型">
          {categorys.map((category) => (
            <Menu.Item
              key={category.value}
              onClick={() => handleFilterChange("category", category)}
            >
              {category.label}
            </Menu.Item>
          ))}
        </Menu.SubMenu> */}
        {destinations.length > 0 &&
          <Menu.SubMenu title="目的地" key="目的地">
            {destinations.map((destination) => (
              <Menu.Item
                key={destination.value}
                onClick={() =>
                  handleFilterChange("destination", destination)
                }
              >
                {destination.label}
              </Menu.Item>
            ))}
          </Menu.SubMenu>
        }
        <Menu.SubMenu title="价格" key="价格">
          {prices.map((price) => (
            <Menu.Item
              key={price.value}
              onClick={() => handleFilterChange("price", price)}
            >
              {price.label}
            </Menu.Item>
          ))}
        </Menu.SubMenu>
        {/* <Menu.Item key="清除" onClick={handleClear}>清除</Menu.Item>
        <Menu.Item key="筛选" onClick={handleSubmit}>筛选</Menu.Item> */}
      </Menu>

      
      <div className="selected-filters">
        {filters.category && (
          <Tag closable onClose={() => handleFilterChange("category", "")}>
            {filters.category.label}
          </Tag>
        )}
        {filters.destination && (
          <Tag closable onClose={() => handleFilterChange("destination", "")}>
            {filters.destination.label}
          </Tag>
        )}
        {filters.price && (
          <Tag closable onClose={() => handleFilterChange("price", "")}>
            {filters.price.label}
          </Tag>
        )}
      </div>

      <style>{`
        .selected-filters {
          margin-top: 10px;
          margin-bottom: 10px;
        }
        .selected-filters :global(.ant-tag) {
          margin-right: 8px;
          margin-bottom: 8px;
        }
      `}</style>
    </>
  );
}

export default function TravelList() {
  const [filters, setFilters] = useState({});
  const [travelList, setTravelList] = useState([]);

  useEffect(() => {
    const options = Object.keys(filters).length > 0 
                  ? `${new URLSearchParams(filters).toString()}`
                  : '';
    get_all_travel(options).then((res) => {
      setTravelList(res);
    });
  }, [filters]);


  function onSelect(filters) {
    setFilters(filters)
  }

  return (
    <>
      <Filter onSelect={onSelect}/>
      <TravelCards travelList={travelList} />
    </>
  );
}
